import React, {FC, useEffect} from 'react'
import {NavLink} from 'react-router-dom'
import s from './Sidebar.module.css'
import {PATH} from '../Pages'
import closeIcon from './closeOutline.svg'

type PropsType = {
    open: boolean
    handleClose: () => void
    handleOpen: () => void
}

export const Sidebar: FC<PropsType> = ({open, handleClose, handleOpen}) => {
    const sidebarClass = `${s.sidebar} ${open ? s.open : s.close}`;

    const handleKeyDown  = (e: KeyboardEvent) => {
        if (e.key === 'Tab') {
            e.preventDefault();
            if (open) {
                handleClose();
            } else {
                handleOpen();
            }
        }
    }
    useEffect(() => {
        window.addEventListener('keydown', handleKeyDown);

        return () => {
            window.removeEventListener('keydown', handleKeyDown);
        }
    }, [open])

    return (
        <>
            {/*затемнение справа от открытого меню*/}
            {open && <div className={s.background} onClick={handleClose}/>}

            <aside className={sidebarClass} onBlur={handleClose}>
                <button className={s.close} onClick={handleClose}>
                    <img
                        src={closeIcon}
                        alt="close sidebar"
                        id={'hw5-menu-close'}
                    />
                </button>

                <nav id={'hw5-menu'} className={`${s.nav} ${open && s.active}`}>
                    <NavLink
                        id={'hw5-pre-junior-link'}
                        to={PATH.PRE_JUNIOR}
                        onClick={handleClose}
                        className={({ isActive }) => isActive ? s.active : ''}
                        // className={...} // делает студент
                    >
                        Pre-junior
                    </NavLink>
                    <NavLink
                        id={'hw5-junior-link'}
                        to={PATH.JUNIOR}
                        onClick={handleClose}
                        className={({ isActive }) => isActive ? s.active : ''}
                        // className={...} // делает студент
                    >
                        Junior
                    </NavLink>
                    <NavLink
                        id={'hw5-junior-plus-link'}
                        to={PATH.JUNIOR_PLUS}
                        onClick={handleClose}
                        className={({ isActive }) => isActive ? s.active : ''}
                        // className={...} // делает студент
                    >
                        Junior Plus
                    </NavLink>
                </nav>
            </aside>
        </>
    )
}
